
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form-section {
  margin: 0 auto;
}

.form {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 400px;
  margin: auto;
}

.form input {
  background: transparent;
  border: 0;
  border-bottom: 2px solid darkgray;
  padding: 5px 0;
  text-align: center;
  font-size: 16px;
  font-family: inherit;
  font-weight: 400;
  outline: none;
}

.form input:focus {
  border-bottom: 2px solid gray;
}

.form input:focus::-webkit-input-placeholder {
  color: transparent
}

.form input::-webkit-input-placeholder {
  color: #bababa;
}

.form input::-ms-input-placeholder {
  color: #bababa;
}

.form input::placeholder {
  color: #bababa;
}

.form button {
  margin-top: 10px;
  background: transparent;
  border: 2px solid darkgray;
  padding: 5px 10px;
  border-radius: 50px;
  cursor: pointer;
  font-weight: 700;
  transition: all 200ms ease;
  color: darkgray;
}

.form button:hover {
  border: 2px solid gray;
  color: gray;
}

.currentinfo-section {
  margin: 20px 0;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  text-align: center;
}

.currentinfo-main {
  display: flex;
  justify-content: center;
}

.currentinfo-main span {
  font-size: 40px;
  margin-left: 10px;
}

.currentinfo-desc {
  font-size: 22px;
  padding: 0;
  margin: 0;
}

.update-info {
  font-size: 14px;
  font-weight: 500;
}

.additional-info {
  display: flex;
  font-size: 14px;
}

.additional-info :first-child {
  margin-right: 10px;
}

.additional-info p {
  margin: 0;
}

.forecast-section {
  max-width: 700px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.dayinfo-content {
  list-style: none;
  text-align: center;
  margin: 5px 0;
  padding: 5px 0;
  width: 120px;
  height: 170px;
  /* border: 1px solid gray;
  border-radius: 10px;
  box-shadow: 0 0 8px rgba(0,0,0,0.5); */
}

.daysinfo-item img {
  margin: 0 auto;
}

.daysinfo-item p {
  margin: 0;
}

.daysinfo-temp {
  font-weight: 500;
  font-size: 20px;
}

.daysinfo-temp span {
  font-weight: 300;
  font-size: 17px;
}

.daysinfo-description {
  font-size: 14px;
}

p {
  margin: 0;
  padding: 0;
}

h4 {
  text-align: center;
}

.hourlyinfo__section .slick-slide {
  width: 120px;
}

.hourlyinfo__section {
  max-width: 700px;
  margin: 0 auto;
  margin-bottom: 30px;
}

.hourlyinfo-content {
  list-style: none;
  text-align: center;
  margin: 5px 0;
  padding: 5px 0;
  width: 120px;
  height: 170px;
  /* border: 1px solid gray;
  border-radius: 10px;
  box-shadow: 0 0 8px rgba(0,0,0,0.5); */
}


.hourlyinfo-item img {
  margin: 0 auto;
}

.hourlyinfo-item p {
  margin: 0;
}

.hourlyinfo-temp {
  font-weight: 500;
  font-size: 20px;
}

.hourlyinfo-description {
  font-size: 14px;
}

body {
  width: 100%;
  height: 100%;
  background: rgba(10,194,255,1);/* Old Browsers */ /* FF3.6+ */
  background: -webkit-gradient(left top, right bottom, color-stop(0%, rgba(10,194,255,1)), color-stop(29%, rgba(194,240,255,1)), color-stop(78%, rgba(0,196,255,1)), color-stop(100%, rgba(138,226,255,1)));/* Chrome, Safari4+ */ /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE 10+ */
  background: linear-gradient(135deg, rgba(10,194,255,1) 0%, rgba(194,240,255,1) 29%, rgba(0,196,255,1) 78%, rgba(138,226,255,1) 100%);/* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0ac2ff', endColorstr='#8ae2ff', GradientType=1 );/* IE6-9 fallback on horizontal gradient */
}

.spinner {
	position: absolute;
	top: 50%;
	left: 50%;
}

@media screen and (max-width: 768px) {
  .slick-arrow {
    display: none !important;
  }
}

@media screen and (max-width: 426px) {
  .form button {
    display: none;
  }
}

